import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
  selector: '[appNumerics]'
})
export class NumericsDirective {

  constructor(private _el: ElementRef) { }

  @HostListener('input', ['$event']) onInputChange(event: KeyboardEvent) {
    let _value = this._el.nativeElement.value;
    //let regex = /^[-?|\+?]\d$/;
    let regex = /\+?\d+/;
    console.log('_value::', _value, 'regex::', regex.test(_value));
    if (regex.test(_value)) {
      console.log('Am I here');
      this._el.nativeElement.value = _value;
    } else {
      console.log('HERE');
      this._el.nativeElement.value = _value.replace(/[^\d]/g, '');
      //event.preventDefault();
    }
  }
}
