import { Component, OnInit } from '@angular/core';
import * as Excel from "exceljs";
import { saveAs } from "file-saver";
import * as _ from 'lodash';

@Component({
  selector: 'app-excel-export',
  templateUrl: './excel-export.component.html',
  styleUrls: ['./excel-export.component.scss']
})
export class ExcelExportComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  toExcel(params) {
    console.log('Excel Data:', params);
    let { _dates, rawData, netUnits, whs, sheetName, type } = params;

    let _dateHeader = [];
    _dates.forEach((i, index) => {
      _dateHeader.push({
        header: i,
        key: 'date_units_' + index,
        width: 15
      })
    });

    const workbook = new Excel.Workbook();
    const sheet = workbook.addWorksheet('Wos');

    /* Handling Columns */
    sheet.columns = [
      { header: '', key: 'item_header', width: 10 },
      { header: '', key: 'whs_header', width: 20 },
      { header: 'Date', key: 'date', width: 40 },
      ..._dateHeader,
      { header: '', key: 'net_units', width: 15 }
    ];

    /* Handle Rows */

    /* Logic to Handle Row #1 */
    const row = sheet.getRow(2);
    row.getCell('item_header').value = type.header;
    row.getCell('whs_header').value = 'WHS';
    row.getCell('date').value = 'Source';
    row.getCell('net_units').value = 'Net Units Total';
    _dates.forEach((item, i) => {
      row.getCell('date_units_' + i).value = 'Net Units';
    });

    /* Logic to Handle Rest of Rows */
    let _item = '';

    rawData.forEach((_el: object, i) => {
      let _restOfRows = sheet.getRow(i + 3);
      if (i == 0) {
        _item = _el[type.key];
      }

      _restOfRows.getCell('item_header').value = _item;
      _restOfRows.getCell('whs_header').value = whs.toString();
      _restOfRows.getCell('date').value = Object.keys(_el['source'][0])[0];
      _restOfRows.getCell('net_units').value = netUnits[i];

      /* Alignment */
      _restOfRows.getCell('net_units').alignment = { vertical: 'middle', horizontal: 'right' };

      _el['source'][0][Object.keys(_el['source'][0])].forEach((_units, _index) => {
        _restOfRows.getCell('date_units_' + _index).value = Math.round(_units.units).toLocaleString();

        /* Alignment */
        _restOfRows.getCell('date_units_' + _index).alignment = { vertical: 'middle', horizontal: 'right' };
      });
    });

    (async function () {
      const buffer = await workbook.xlsx.writeBuffer();
      const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
      const fileExtension = '.xlsx';
      const blob = new Blob([buffer], { type: fileType });
      saveAs(blob, sheetName + fileExtension);
    })();
  }

}
