import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WeeklySupplyComponent } from './views/weekly-supply/weekly-supply.component';
import { WosCategoryComponent } from './views/wos-category/wos-category.component';
import { WosMasterComponent } from './views/wos-master/wos-master.component';
import { WosWarehouseComponent } from './views/wos-warehouse/wos-warehouse.component';


const routes: Routes = [
  { path: '', component: WosWarehouseComponent },
  { path: 'warehouse-aggregation', component: WosWarehouseComponent },
  { path: 'sub-category-aggregation', component: WosCategoryComponent },
  { path: 'master-item-aggregation', component: WosMasterComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
